import React from "react";

function ButtonGreen({ title, onClick, disabled, className }) {
  return (
    <>
      <button
        className={`btn ${className ? className : "btn-secondary"} ${
          disabled ? "disabled" : ""
        }`}
        onClick={onClick}
      >
        {title}
      </button>
    </>
  );
}

export default ButtonGreen;
