import { ROUTE_CONSTANTS } from "shared/routeConstants";

function NavBarHOC(props) {
  const history = useHistory();
  return (
    <>
      <aside className="sideBar">
        <button className ="d-lg-none close_btn"><img src={Images.CloseIcon} alt="Close" width="30" /></button>
        <nav className ="navbar text-left">
          <div className="navbar-brand" onClick={()=> history.push(ROUTE_CONSTANTS.DASHBOARD)}>
            <img src={Images.locumLogo} alt="locumlogo" width="194" className="img-fluid" />
          </div>
          <ul className="navbar-nav w-100">
            {navbarData_Admin.map((item,idx) => {
              return <SideItem key={idx} data={item} />;
            })}
          </ul>
        </nav>
      </aside>
    </>
  );
}

export default NavBarHOC;
