import React from "react";
import { PHARMACY_USER_VIEW } from "shared/constants";
import { Images } from "shared/images";
import { JOB_TIMING } from "shared/tableData";

function BasicView({ data, apiData }) {
  console.log(data, "DATA");
  return (
    <div>
      <div className="d-flex bg-light m-3">
        {data.length
          ? data?.map((i, idx) => {
              return (
                <div className="m-5">
                  <span>{i?.title ? i?.title : ""}</span>
                  <h6>
                    {i?.value ? i?.value : ""}
                    {i?.isVerified ? (
                      <img alt="" src={Images.verifyLogo} />
                    ) : null}
                  </h6>
                </div>
              );
            })
          : null}
      </div>

      <div className="d-flex bg-light m-3">
        <div className="m-3">
          <h6>{PHARMACY_USER_VIEW.DESCRIPTIONS}</h6>
          <span>{apiData.description}</span>
        </div>
      </div>

      {/* <div className="d-flex bg-light m-3">
        <div className="m-3">
          <h6>{PHARMACY_USER_VIEW.SERVICES}</h6>
          <span>Coming Soon </span>
        </div>
      </div> */}

      <div className="d-flex bg-light m-3">
        <div className="m-3">
          <h6>{PHARMACY_USER_VIEW.JOB_TIMINGS}</h6>
          <table>
            <thead>
              <tr key={1000 + 1}>
                <td></td>
                {Object.values(JOB_TIMING.HEADER).map((i, idx) => {
                  return (
                    <>
                      <th id={idx}>{i}</th>
                    </>
                  );
                })}
              </tr>

              {apiData?.job_timings_info?.length &&
                apiData?.job_timings_info?.map((i, idx) => (
                  <tr>
                    <th>{JOB_TIMING.WEEKDAYS[i?.day]}</th>
                    <td> {i?.opening_time} </td>
                    <td> {i?.closing_time} </td>
                    <td> {i?.lunch_start} </td>
                    <td> {i?.lunch_end} </td>
                  </tr>
                ))}
            </thead>
            <tbody></tbody>
          </table>
        </div>
      </div>
    </div>
  );
}

export default BasicView;
