import React from "react";
import Modal from "react-modal";
import { Images } from "shared/images";
import "./modal.scss";
function CustomModal(
  {
    children,
    className = "",
    handleToggle = () => { },
    isOpen = false,
    size = "",
    style = {},
    clearHandler = () => { },
  },
  props
) {
  return (
    <>
      <Modal
        isOpen={isOpen}
        onRequestClose={handleToggle}
        className={`Modal react_modal_main ${size ? size : ""}`}
        overlayClassName="Overlay"
        ariaHideApp={false}
      >
        <div
          className={`modal-dialog modal-dialog-centered ${size ? size : ""}`
            // className ? className : "modal-dialog modal-dialog-centered"
          }
        >
          <div className="modal-content">
            <div className="modal-body">
              <button className="btn-close btn-transparent"
                onClick={() => {
                  handleToggle();
                  clearHandler();
                }}
              >
                <img src={Images.close} alt="Close" width="14" />
              </button>
              {children}
            </div>
          </div>
        </div>
      </Modal>
    </>
  );
}

export default CustomModal;
