import React, { useState } from "react";
import ReactPaginate from "react-paginate";
import "./style.scss";
import { Images } from "shared/images";
import CheckClickOutside from "../checkOutsideClick/checkOutsideClick";
import { PAGE_COUNT } from "shared/constants";
import DialogView from "../dialogview";

export function Pagination({
  items,
  setPageCount,
  setItemsPerPage,
  itemsPerPage,
  userStatusChange,
}) {
  // Here we use item offsets; we could also use page offsets
  // following the API or data you're working with.
  const [itemOffset, setItemOffset] = useState(0);

  // Simulate fetching items from another resources.
  // (This could be items from props; or items loaded in a local state
  // from an API endpoint with useEffect and useState)
  const endOffset = itemOffset + itemsPerPage;
  console.log(`Loading items from ${itemOffset} to ${endOffset}`);
  // const currentItems = items?.slice(itemOffset, endOffset);
  const pageCount = Math.ceil(items / itemsPerPage);
  const [pageCountShow, setPageCountShow] = useState();

  // Invoke when user click to request another page.
  const handlePageClick = (event) => {
    const newOffset = (event.selected * itemsPerPage) % items;
    console.log(
      `User requested page number ${event.selected}, which is offset ${newOffset}`
    );
    setItemOffset(newOffset);
    setPageCount(event.selected+1);
  };

  return (
    <div>
      <div className="d-flex">
        <span>Entries</span>
        <CheckClickOutside onClick={() => setPageCountShow(false)}>
          <button
            className="p-0 btn-transparent arrow_down"
            onClick={() => {
              setPageCountShow((prev) => !prev);
            }}
          >
            <label>{itemsPerPage}</label>
            <img src={Images.arrowDown} alt="" width={20} />
          </button>
          <div hidden={!pageCountShow}>
            <DialogView>
              {PAGE_COUNT.map((i, idx) => {
                return (
                  <div
                    key={idx}
                    className="dropDown-list"
                    onClick={() => {
                      setPageCountShow((prev) => !prev);
                      setItemsPerPage(i);
                    }}
                  >
                    <span>{i}</span>
                  </div>
                );
              })}
            </DialogView>
          </div>
        </CheckClickOutside>
        <span>{`Showing 1 to ${itemsPerPage} of ${items} Entries`}</span>
      </div>
      <ReactPaginate
        breakLabel="..."
        nextLabel="next >"
        onPageChange={handlePageClick}
        pageRangeDisplayed={5}
        pageCount={pageCount}
        previousLabel="< previous"
        renderOnZeroPageCount={null}
        className="d-flex justify-content-end pagination"
      />
    </div>
  );
}
