import React from 'react'
import './style.scss';

function NavTab({ tabsData = [], activeTab, onClick = () => { } }) {
  return (
    <div className="d-flex nav-tabs list-unstyled m-0 p-0">
      {tabsData.map((item, idx) => (
        <li
          key={idx}
          onClick={() => onClick(item.id)}
          className={`show ${item.id === activeTab && "active"} `}
        >
          {item.title}
        </li>
      ))}
    </div>
  )
}

export default NavTab
