function* loginAuth({ payload, onSuccess = () => {}, onError = () => {} }) {
  try {
    yield put(startLoader());
    let data = objectToFormData(payload);
    const response = yield postRequest({
      API: API.LOGIN,
      DATA: data,
    });

    if (
      response.status === STATUS_CODES.SUCCESS ||
      response.status === STATUS_CODES.SUCCESS_VERIFIED
    ) {
      yield put(setAuthToken(response.data.token));
      updateAuthToken(response.data.token);
      yield put(saveAdminDetails(response.data.data));
      onSuccess(response.status, API_RESPONSE.SUCCESS);
    } else {
      onError(response.data.message, API_RESPONSE.ERROR);
    }
  } catch (error) {
    onError(error.data.message, API_RESPONSE.ERROR);
  } finally {
    yield put(stopLoader());
  }
}

function* logoutAuth({ payload, onSuccess = () => {}, onError = () => {} }) {
  try {
    yield put(startLoader());
    const response = yield postRequest({
      API: API.LOGOUT,
    });

    if (
      response.status === STATUS_CODES.SUCCESS ||
      response.status === STATUS_CODES.SUCCESS_VERIFIED
    ) {
      yield put(setAuthToken(""));
      updateAuthToken("");
      yield put(stopLoader());
    } else if (response.status === STATUS_CODES.UNAUTHORIZED) {
      yield put(setAuthToken(""));
      updateAuthToken("");
    } else {
      onError(response.data.message, API_RESPONSE.ERROR);
    }
  } catch (error) {
    if (error.status === STATUS_CODES.UNAUTHORIZED) {
      yield put(setAuthToken(""));
      updateAuthToken("");
    }
    onError(API_RESPONSE.ERROR);
  } finally {
    yield put(stopLoader());
  }
}

