function* watchAuth() {
  yield all([
    takeLatest(LOGIN, loginAuth),
    takeLatest(LOGOUT, logoutAuth),
    takeLatest(RESET_PASSWORD, resetPasswordAdmin),
    takeLatest(GET_DASHBOARD_DATA, getDashboardDataAdmin),
    takeLatest(GET_UNVERIFIED_USERS, getUnverifiedUserList),
    takeLatest(FORGOT_PASSWORD, forgotPasswords),
    takeLatest(CONFIRM_PASSWORD, confirmPassword),
    takeLatest(CHANGE_PASSWORD, changePassword),
    takeLatest(GET_USER_LISTING, fetchUserListing),
    takeLatest(EDIT_ADMIN_PROFILE, updateAdminProfile),
    takeLatest(USER_STATUS_UPDATE, userStatusUpdate),
    takeLatest(DOCUMENT_STATUS_CHANGE, documentStatusChange),
    takeLatest(DELETE_USERS, deleteUsers),
    takeLatest(VIEW_USER_DETAILS, fetchUserDetails),
    takeLatest(BRANCH_VIEW, fetchBranchView),
    takeLatest(BRANCH_LISTING, fetchBranchListing),
    takeLatest(DELETE_BRANCH, deleteBranch),
    takeLatest(INVOICE_VIEW, fetchInvoiceView),
    takeLatest(INVOICE_LISTING, fetchInvoiceListing),
    takeLatest(PHARMACY_LISTING_DELETE, deletePharmacyService),
    takeLatest(PHARMACY_LISTING_ADD, addPharmacyService),
    takeLatest(PHARMACY_LISTING_VIEW, fetchPharmacyServiceView),
    takeLatest(PHARMACY_LISTING_FETCH, fetchPharmacyServiceListing),
    takeLatest(PHARMACY_LISTING_UPDATE_STATUS, statusPharmacyService),
    takeLatest(PHARMACY_LISTING_EDIT, editPharmacyService),
    takeLatest(CS_IN_USE_DELETE, deleteCSinUse),
    takeLatest(CS_IN_USE_ADD, addCSinUse),
    takeLatest(CS_IN_USE_FETCH, fetchCSinUseListing),
    takeLatest(CS_IN_USE_UPDATE_STATUS, statusCSinUse),
    takeLatest(CS_IN_USE_EDIT, editCSinUse),
    takeLatest(JOB_DETAILS_USER, jobDetails),
  ]);
}

export default watchAuth;
