import React, { useCallback, useEffect, useState } from "react";
import { ROUTE_CONSTANTS } from "../../../../shared/routeConstants";
import { useHistory, useLocation } from "react-router-dom";
import {
  BRANCHES_VIEW,
  PHARMACY_USER_VIEW,
  SNACK_MESSAGES,
  SNACK_VARIANT,
  TAB_BRANCHES_VIEW,
} from "shared/constants";
import { Images } from "shared/images";
import NavTab from "components/atom/tabNav";
import { branches_basic_info } from "./constant";
import BasicView from "./BasicView";
import { useDispatch } from "react-redux";
import useNotification from "components/atom/snackbar";
import { branchViewApi, deleteBranchsApi } from "redux/actions/auth";
import { Pagination } from "components/atom/pagination";
import SearchBar from "components/atom/searchBar";

function BranchesView({}) {
  const history = useHistory();
  const dispatch = useDispatch();
  const idParam = useLocation();
  const [activeItem, setActiveItem] = useState(1);
  const id = idParam?.state?.id;
  const [viewData, setViewData] = useState({});
  const [data, setData] = useState({});
  const [msg, sendNotification] = useNotification();

    const BASIC_CONST = [
      {
        title: "Pharmacy Name",
        value: viewData?.pharmacy_name,
        isVerified: true,
      },
      {
        title: "Branch Name",
        value: viewData?.branch_name,
      },
      {
        title: "Country",
        value: viewData?.country,
      },
      {
        title: "City",
        value: viewData?.city,
      },
      {
        title: "Post Code",
        value: viewData?.postal_code,
      },
      {
        title: "Street No / Name",
        value: viewData?.address,
      },
      {
        title: "Phone",
        value: viewData?.phone_number,
      },
  
      {
        title: "Branch Email",
        value: viewData?.branch_email,
      },
      {
        title: "Number of Staff",
        value: viewData?.number_of_staff,
      },
      {
        title: "Number of Shifts Allowed",
        value: viewData?.number_of_shift_allowd,
      },
    ];
  

  const deleteBranch = (id) => {
    let data = {
      branches: [
        {
          id: id,
        },
      ],
    };
    dispatch(
      deleteBranchsApi(data, (res) => {
        sendNotification({
          msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
          variant: SNACK_VARIANT.SUCCESS,
        });
      }),
      (err) => {
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    );
  };

  const DataSwitch = () => {
    switch (activeItem) {
      case BRANCHES_VIEW.BASIC_BRANCH_INFO.ID:
        return <BasicView data={BASIC_CONST} apiData={viewData}/>;
      case BRANCHES_VIEW.VACANCIES.ID:
        return <>No Data</>;
      default:
    }
  };

  const viewUser = useCallback(() => {
    let data = { id: id };
    dispatch(
      branchViewApi(data, (res) => {
        setViewData(res.data);
      }),
      (err) => {
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
        history.goBack();
      }
    );
  }, [id, dispatch, sendNotification, history]);

  useEffect(() => {
    viewUser();
  }, [viewUser]);

  return (
    <div>
      <div className="d-flex-row">
        <span onClick={() => history.push(ROUTE_CONSTANTS.USERS)}>
          {PHARMACY_USER_VIEW.PHARMACY_BRANCHES}
        </span>
        <img src={Images.arrowRight} alt="" />
        <span>{PHARMACY_USER_VIEW.DETAILS}</span>

        <button className={`btn btn-danger`} onClick={() => deleteBranch(id)}>
          Delete
        </button>
      </div>
      <NavTab
        onClick={(id) => setActiveItem(id)}
        tabsData={TAB_BRANCHES_VIEW}
        activeTab={activeItem}
      />
      <div>
        <DataSwitch />
      </div>
    </div>
  );
}

export default BranchesView;
