import CustomTable from "components/atom/table";
import { getLocalizedDateString } from "components/utility";
import React from "react";
import { PHARMACY_USER_VIEW } from "shared/constants";
import { Images } from "shared/images";
import {
  INVOICE_JOB_TABLE,
} from "shared/tableData";

function BasicInvoiceView({ data, apiData }) {
  console.log(data, apiData, "DATA");

const invoice_Extras = [
    {
      title: "Lunch",
      value: apiData?.extras?.lunch,
    },
    {
      title: "Mileage to be paid for travel",
      value: apiData?.extras?.mileage_to_be_paid,
    },
    {
      title: "Total (Before tax)",
      value: apiData?.extras?.total_before_taxes,
    },
    {
      title:"Taxes",
      value: apiData?.extras?.taxes,
    },
    {
      title:"Total",
      value: apiData?.extras?.total,
    },
  ];

  return (
    <div>
      <div className="d-flex bg-light m-3">
        {data.length
          ? data?.map((i, idx) => {
              return (
                <div className="m-5">
                  <span>{i?.title ? i?.title : ""}</span>
                  <h6>
                    {i?.value ? i?.value : ""}
                    {i?.isVerified ? (
                      <img alt="" src={Images.verifyLogo} />
                    ) : null}
                  </h6>
                </div>
              );
            })
          : null}
      </div>

      <div className="d-flex bg-light m-3">
        <div className="m-3">
          <h6>{PHARMACY_USER_VIEW.COMPLETED_JOBS}</h6>
          <div className="customTable">
            <CustomTable>
              <thead>
                <tr key={1000 + 1}>
                  {Object.values(INVOICE_JOB_TABLE).map((i, idx) => (
                    <th scope="col" id={idx}>
                      {i}
                    </th>
                  ))}
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>{1}</td>
                  <td>{apiData.jobDetails?.job_id}</td>
                  <td>{apiData?.jobDetails?.invoice_id}</td>
                  <td>{getLocalizedDateString(apiData?.jobDetails?.job_date)}</td>
                  <td>{apiData?.jobDetails?.hourly_fee}</td>
                  <td>{apiData?.jobDetails?.no_of_hours}</td>
                  <td>{apiData?.jobDetails?.amount}</td>
                </tr>
              </tbody>
            </CustomTable>
          </div>
        </div>
      </div>

      <div className="d-flex m-3">
        <div className="m-3">
          <h6>{PHARMACY_USER_VIEW.EXTRAS}</h6>
          <div className="d-flex m-5">
            <div className="col-sm-6">
              <table class="table">
                <tbody>
                  {invoice_Extras
                    ? invoice_Extras?.map((i, idx) => {
                        return (
                          <>
                            <tr id={idx}>
                              <td>{i?.title}</td>
                              <td>{":"}</td>
                              <td>{i?.value}</td>
                            </tr>
                          </>
                        );
                      })
                    : "No Documents Uploaded"}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
      <div className="d-flex m-3">
        <div className="m-3">
          <h6>{PHARMACY_USER_VIEW.LOCUM_TERMS_CONDITIONS}</h6>
          <span>
            The above independent contractor is a registered pharmacist in the
            UK and has performed their duties as a pharmacist in accordance to
            the specified pharmacies Standard Operating Procedures. Any taxes or
            National Insurance payments required for the above income will be
            paid independently by the above named person. Invoices must be
            authorised by branch manager before being paid by accounts. This
            usually takes 14-28 days, however, it can be delayed in some
            circumstances. Claims made after six months from date worked will
            incur an admin fee. Any claims made after 12 months will not be
            processed. No cash payment, locums will be paid direct into their
            account.
          </span>
        </div>
      </div>
      <button className="btn btn-primary" onClick={() => window.print()}>
        Download
      </button>
      <button className="btn btn-primary" onClick={() => window.print()}>
        Print
      </button>
    </div>
  );
}

export default BasicInvoiceView;
