import { Fade, Tooltip } from "@material-ui/core";
import React from "react";
import { STATUS_OPTIONS } from "shared/constants";

function ButtonStatus({ image, status, title, onClick }) {
  return (
    <Tooltip
      title={title}
      arrow={true}
      placement={"bottom"}
      enterDelay={200}
      TransitionComponent={Fade}
      TransitionProps={{ timeout: 300 }}

    >
      <span
        className={`m-1 btn-badge btn-${status !== STATUS_OPTIONS.NOT_UPLOADED.title
          ? `${status}`
          : "notUploaded"
          }`}
        onClick={onClick}
      >
        <img src={image} alt="" width="13" />
      </span>
    </Tooltip>
  );
}

export default ButtonStatus;
