import React from "react";
import CustomModal from "../modal";
import { useState } from "react";
import { Document, Page } from "react-pdf/dist/esm/entry.webpack";
import ButtonGreen from "../button/ButtonGreen";
import { STATUS_OPTIONS } from "shared/constants";

// install npm i react-pdf
// if you face issue then install npm i react-pdf@5.0.0 , for clarity check https://github.com/wojtekmaj/react-pdf/issues/1043
// if you have webpack version less than 4 install(npm i file-loader)

function DocumentViewer({ viewDoc, setViewDoc, isVerified }) {
  const [numPages, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);

  const onDocumentLoadSuccess = ({ numPages }) => {
    setNumPages(numPages);
  };

  const goToPrevPage = () =>
    setPageNumber(pageNumber - 1 <= 1 ? 1 : pageNumber - 1);

  const goToNextPage = () =>
    setPageNumber(pageNumber + 1 >= numPages ? numPages : pageNumber + 1);

  return (
    <div>
        {/* <nav>
          <button onClick={goToPrevPage}>Prev</button>
          <button onClick={goToNextPage}>Next</button>
          <p>
            Page {pageNumber} of {numPages}
          </p>
        </nav> */}
        <Document
          file="https://cdn.openai.com/papers/gpt-4.pdf"
          onLoadSuccess={onDocumentLoadSuccess}
        >
          <Page pageNumber={pageNumber} wrap={true} />
        </Document>
    </div>
  );
}

export default DocumentViewer;
