import TextField from "components/atom/textField/TextField";
import React, { useEffect, useState } from "react";
import { BUTTONS, CS_IN_USE } from "shared/constants";

function CsField({ addEditAPI, buttonText, data }) {
  const [name, setName] = useState("");

  useEffect(() => {
    if (buttonText === BUTTONS.EDIT) {
      setName(data.cs_name ? data.cs_name : "");
    } else {
      setName("");
    }
  }, [buttonText, data.cs_name]);

  return (
    <div>
      <form autoComplete="off">
        <TextField
          label={CS_IN_USE.NAME}
          value={name}
          type="text"
          onChange={(e) => setName(e.target.value)}
        />
        <button
          className="btn btn-primary"
          onClick={(e) => {
            addEditAPI(e, name);
          }}
        >
          {buttonText}
        </button>
      </form>
    </div>
  );
}

export default CsField;
