import React, { useState } from "react";
import { BUTTONS, LOGIN_FORM } from "shared/constants";
import { Images } from "shared/images";
import TextField from "components/atom/textField/TextField";
import ButtonGreen from "../../../components/atom/button/ButtonGreen";

function ResetPass({onUpdate = () => {} }) {
  const [checkPass, setCheckPass] = useState("");
  const [newPass, setNewPass] = useState("");
  const [oldPass, setOldPass] = useState("");
  const [passwordErr, setPasswordErr] = useState("");

  const formData = [
    {
      key:1,
      label: LOGIN_FORM.PASSWORDCHANGEFORM.FIELDS.OLDPASSWORD.LABEL,
      image: Images.ShowPwdIcon,
      value: oldPass,
      onChange: (e) => setOldPass(e.target.value),
    },
    {
      key:2,
      label: LOGIN_FORM.PASSWORDCHANGEFORM.FIELDS.NEWPASSWORD.LABEL,
      image: Images.ShowPwdIcon,
      value: newPass,
      onChange: (e) => setNewPass(e.target.value),
    },
    {
      key:3,
      label: LOGIN_FORM.PASSWORDCHANGEFORM.FIELDS.CONFIRMPASSWORD.LABEL,
      image: Images.ShowPwdIcon,
      value: checkPass,
      error: passwordErr,
      onChange: (e) => {
        setCheckPass(e.target.value);
        if (newPass !== e.target.value) {
          setPasswordErr(
            LOGIN_FORM.PASSWORDCHANGEFORM.FIELDS.CONFIRMPASSWORD.ERROR
          );
        } else {
          setPasswordErr("");
        }
      },
    },
  ];

  return (
    <>
      <form autoComplete="off">
        {formData.map((i, idx) => (
          <TextField
            id={i.key}
            label={i.label}
            image={i.image}
            value={i.value}
            type="password"
            onChange={i.onChange}
          />
        ))}

        <div className="text-center">
          <ButtonGreen title={BUTTONS.UPDATE} onClick={(e) => onUpdate(e,oldPass,newPass,passwordErr)} />
        </div>
      </form>
    </>
  );
}

export default ResetPass;
