import React, { useEffect, useState } from "react";
import useNotification from "components/atom/snackbar";
import { useCallback } from "react";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import { getJobDetailsUser } from "redux/actions/auth";
import { SNACK_MESSAGES, SNACK_VARIANT } from "shared/constants";
import CustomTable from "components/atom/table";
import { getLocalizedDateString } from "components/utility";

function JobView({ id }) {
  const history = useHistory();
  const dispatch = useDispatch();
  const [msg, sendNotification] = useNotification();
  const [data, setData] = useState({});
  const [pageCount, setPageCount] = useState(1);
  const [keyword, setKeyword] = useState("");
  const [itemsPerPage, setItemsPerPage] = useState(15);

  const viewUser = useCallback(() => {
    let data = {
      user_id: id,
      page: pageCount,
      search: keyword,
      limit: itemsPerPage,
    };
    dispatch(
      getJobDetailsUser(data, (res) => {
        setData(res.data);
      }),
      (err) => {
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
        history.goBack();
      }
    );
  }, [id, dispatch, sendNotification, history]);

  const headerData = {
    ID: "#",
    JOB_ID:"Job ID",
    PHARMACY:"Pharmacy",
    BRANCH:"BRANCH",
    JOINED_DATE:"Joined Date",
    RATING:"Rating",
    RATE:"Rate",
    STATUS:"Status",
    ACTION:"Action"
  };

  console.log(data, "JOB DETAILS");

  useEffect(() => {
    viewUser();
  }, [viewUser]);

  return (
    <div>
      {data?.length ? (
        <div className="customTable">
          <CustomTable>
            <thead>
              <tr key={1000 + 1}>
                {Object.values(headerData).map((i, idx) => (
                  <th scope="col" id={idx}>
                    {i}
                  </th>
                ))}
              </tr>
            </thead>
            <tbody>
              {data.length  ? data?.map((i, idx) => {
                return (
                  <tr>
                    <td>{idx + 1}</td>
                    <td>{i?.job_id}</td>
                    <td>{i?.pharmacy}</td>
                    <td>{i?.branch}</td>
                    <td>{getLocalizedDateString(i?.join_date)}</td>
                    <td>{i?.rating}</td>
                    <td>{i?.rate}</td>
                    <td>{i?.status}</td>
                  </tr>
                );
              }) : "No Data Availiable"}
            </tbody>
          </CustomTable>
        </div>
      ) : (
        "No Data Availiable"
      )}
    </div>
  );
}

export default JobView;
