import useNotification from "components/atom/snackbar";
import ActivateDeactivate from "components/molecules/activateDeactivate";
import StatusChangeDropdown from "components/molecules/statusChangeDropdown";
import React, { useCallback, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import {
  InvoiceListingApi,
  branchListingApi,
  deleteBranchsApi,
  deleteInvoiceApi,
  deleteUsersApi,
  pharmacyServiceAdd,
  viewUserDetailsApi,
} from "redux/actions/auth";
import { userStatusUpdate } from "redux/actions/common";
import {
  PHARMACY_DETAILS,
  PHARMACY_USER_VIEW,
  SNACK_MESSAGES,
  SNACK_VARIANT,
  TAB_PHARMACY_USER_DATA,
} from "shared/constants";
import { Images } from "shared/images";
import { ROUTE_CONSTANTS } from "shared/routeConstants";
import BasicPharmacyInfo from "./BasicPharmacyInfo";
import {
  useLocation,
  useParams,
} from "react-router-dom/cjs/react-router-dom.min";
import NavTab from "components/atom/tabNav";
import BranchesView from "../branches/BranchesTable";
import BranchesTable from "../branches/BranchesTable";
import SearchBar from "components/atom/searchBar";
import InvoiceTable from "../invoice/InvoiceTable";

function PharmacyView() {
  const history = useHistory();
  const dispatch = useDispatch();
  const idParam = useLocation();
  const token = useSelector((state) => state.auth.token);
  const [msg, sendNotification] = useNotification();
  const [docType, setDocType] = useState(1);
  const [showStatusModal, setShowStatusModal] = useState(false);
  const [multiplePayload, setMultiplePayload] = useState([]);
  const id = idParam?.state?.id;
  const [userData, setUserData] = useState();
  const [activeItem, setActiveItem] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(15);
  const [pageCount, setPageCount] = useState(1);
  const [pageItems, setPageItems] = useState(1);
  const [keyword, setKeyword] = useState("");
  const [fetchedBranchData, setFetchedBranchData] = useState({});
  const [fetchedInvoiceData, setFetchedInvoiceData] = useState({});
  const [sortKey, setSortKey] = useState(0);

  useEffect(() => {
    if (id) {
      viewUser();
    } else {
      history.goBack();
    }
  }, []);

  const fetchBranchList = useCallback(() => {
    let payload = {
      page: pageCount,
      search: keyword,
      limit: itemsPerPage,
    };
    dispatch(
      branchListingApi(
        payload,
        (res) => {
          setFetchedBranchData(res.data);
          setPageItems(res.count)
        },
        (err) => {
          sendNotification({
            msg: err?.data?.message
              ? err.data.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  }, [activeItem, dispatch,sortKey, itemsPerPage, keyword, pageCount, sendNotification]);

  const fetchInvoiceList = useCallback(() => {
    let payload = {
      page: pageCount,
      search: keyword,
      limit: itemsPerPage,
    };
    dispatch(
      InvoiceListingApi(
        payload,
        (res) => {
          setFetchedInvoiceData(res.data);
          setPageItems(res.count)
        },
        (err) => {
          sendNotification({
            msg: err?.data?.message
              ? err.data.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  }, [activeItem, dispatch,sortKey, itemsPerPage, keyword, pageCount, sendNotification]);

  useEffect(() => {
    fetchBranchList();
  }, [fetchBranchList,sortKey, itemsPerPage, pageCount]);

  useEffect(() => {
    fetchInvoiceList();
  }, [fetchInvoiceList,sortKey, itemsPerPage, pageCount]);


  const userStatusChange = (id_, is_active) => {
    let arr = [
      {
        user_id: id,
        is_active: is_active,
      },
    ];

    let data = {
      user_status_update: arr,
    };

    dispatch(
      userStatusUpdate(
        data,
        (res) => {
          sendNotification({
            msg: res?.data?.message
              ? res.data.message
              : SNACK_MESSAGES.SAVED_SUCCESSFULLY,
            variant: SNACK_VARIANT.SUCCESS,
          });
        },
        (err) => {
          sendNotification({
            msg: err?.data?.message
              ? err.data.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  const deleteUsers = (id) => {
    let data = {
      users: [
        {
          id: id,
        },
      ],
    };
    dispatch(
      deleteUsersApi(data, (res) => {
        viewUser();
        sendNotification({
          msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
          variant: SNACK_VARIANT.SUCCESS,
        });
      }),
      (err) => {
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    );
  };

  const deleteBranch = (id) => {
    let data = {
      branches: [
        {
          id: id,
        },
      ],
    };
    dispatch(
      deleteBranchsApi(data, (res) => {
        fetchBranchList();
        sendNotification({
          msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
          variant: SNACK_VARIANT.SUCCESS,
        });
      }),
      (err) => {
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    );
  };

  const deleteInvoice = (id) => {
    let data = {
      invoices: [
        {
          id: id,
        },
      ],
    };
    dispatch(
      deleteInvoiceApi(data, (res) => {
        fetchInvoiceList();
        sendNotification({
          msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
          variant: SNACK_VARIANT.SUCCESS,
        });
      }),
      (err) => {
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    );
  };

  const viewUser = useCallback(() => {
    let data = {
      user_id: id,
    };
    dispatch(
      viewUserDetailsApi(data, (res) => {
        setUserData(res.data);
      }),
      (err) => {
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
        history.goBack();
      }
    );
  }, [id, dispatch, sendNotification, history]);

  const DataSwitch = () => {
    switch (activeItem) {
      case PHARMACY_DETAILS.BASIC_PHARMACY_INFO.ID:
        return <BasicPharmacyInfo data={userData} reload={viewUser} />;
      case PHARMACY_DETAILS.VACANCIES.ID:
        return <>No Data</>;
      case PHARMACY_DETAILS.BRANCHES.ID:
        return (
          <BranchesTable
            multiplepayload={multiplePayload}
            setmultiplePayload={setMultiplePayload}
            navigateBranches={(id) =>
              history.push(ROUTE_CONSTANTS.BRANCHES_VIEW, { id: id })
            }
            data={fetchedBranchData}
            deleteBranch={deleteBranch}
            itemsPerPage={itemsPerPage}
            setItemsPerPage={setItemsPerPage}
            pageCount={pageCount}
            setPageCount={setPageCount}
            sortKey={sortKey}
            setSortKey={setSortKey}
            pageItems={pageItems}
            setPageItems={setPageItems}
          />
        );
      case PHARMACY_DETAILS.TEMPLATE.ID:
        return <>No Data</>;
      case PHARMACY_DETAILS.INVOICES.ID:
        return (
          <InvoiceTable
            multiplepayload={multiplePayload}
            setmultiplePayload={setMultiplePayload}
            navigateBranches={(id) =>
              history.push(ROUTE_CONSTANTS.INVOICE_VIEW, { id: id })
            }
            data={fetchedInvoiceData}
            deleteBranch={deleteInvoice}
            itemsPerPage={itemsPerPage}
            setItemsPerPage={setItemsPerPage}
            pageCount={pageCount}
            setPageCount={setPageCount}
            sortKey={sortKey}
            setSortKey={setSortKey}
            pageItems={pageItems}
            setPageItems={setPageItems}
          />
        );
      default:
    }
  };

  const searchAPI_Switch = () => {
    switch (activeItem) {
      case PHARMACY_DETAILS.BASIC_PHARMACY_INFO.ID:
        return viewUser;
      case PHARMACY_DETAILS.BRANCHES.ID:
        return fetchBranchList;
      default:
    }
  };

  const Delete_Switch = () => {
    switch (activeItem) {
      case PHARMACY_DETAILS.BASIC_PHARMACY_INFO.ID:
        return deleteUsers;
      case PHARMACY_DETAILS.BRANCHES.ID:
        return deleteBranch;
      default:
    }
  };

  return (
    <div>
      <div className="d-flex-row">
        <span onClick={() => history.push(ROUTE_CONSTANTS.USERS)}>
          {PHARMACY_USER_VIEW.PHARMACY_USERS}
        </span>
        <img src={Images.arrowRight} alt="" />
        <span>{PHARMACY_USER_VIEW.DETAILS}</span>

        {/* <ActivateDeactivate
          title={"Select Action"}
          statusChange={userStatusChange}
          value={{ id: id }}
        />
        <StatusChangeDropdown
          statusChange={setDocType}
          setDocType={setDocType}
          setShow={setShowStatusModal}
        />
        <button className={`btn btn-danger`} onClick={Delete_Switch()}>
          Delete
        </button> */}
        {multiplePayload.length &&
        PHARMACY_DETAILS.BRANCHES.ID === activeItem ? (
          <button className={`btn btn-danger`} onClick={Delete_Switch()}>
            Delete
          </button>
        ) : null}
        {PHARMACY_DETAILS.BASIC_PHARMACY_INFO.ID === activeItem ? null : (
          <SearchBar
            searchAPI={() => searchAPI_Switch()}
            keyword={keyword}
            setKeyword={setKeyword}
          />
        )}
      </div>
      <NavTab
        onClick={(id) => setActiveItem(id)}
        tabsData={TAB_PHARMACY_USER_DATA}
        activeTab={activeItem}
      />
      <div>
        <DataSwitch />
      </div>
    </div>
  );
}

export default PharmacyView;
