import TextField from "components/atom/textField/TextField";
import React, { useEffect, useState } from "react";
import { BUTTONS, PHARMACY_SERVICES } from "shared/constants";

function ServiceField({ addEditServiceAPI, buttonText, serviceData }) {
  const [serviceName, setServiceName] = useState("");

  useEffect(() => {
    if (buttonText === BUTTONS.EDIT) {
      setServiceName(serviceData.service_name ? serviceData.service_name : "");
    } else {
      setServiceName("");
    }
  }, [buttonText, serviceData.service_name]);

  return (
    <div>
      <form autoComplete="off">
        <TextField
          label={PHARMACY_SERVICES.SERVICE_NAME}
          value={serviceName}
          type="text"
          onChange={(e) => setServiceName(e.target.value)}
        />
        <button
          className="btn btn-primary"
          onClick={(e) => {
            addEditServiceAPI(e, serviceName);
          }}
        >
          {buttonText}
        </button>
      </form>
    </div>
  );
}

export default ServiceField;
