import React, { useState } from "react";
import { STATUS_DROPDOWN, STATUS_OPTIONS } from "shared/constants";
import Select from "react-select";

function StatusChangeDropdown({ value, statusChange, setDocType, setShow }) {
  const [showModal, setShowModal] = useState(false);
  const options = STATUS_DROPDOWN.map((i) => ({
    value: i.key,
    label: i.title,
  }));

  const handleChange = (selectedOption) => {
    setDocType(selectedOption.value);
    setShow(true);
  };

  return (
    <Select
      options={options}
      placeholder={STATUS_OPTIONS.PLACEHOLDER}
      onChange={handleChange}
    />
  );
}

export default StatusChangeDropdown;
