import React from "react";
import Select from "react-select";

const options = [
  { value: true, label: "Activate" },
  { value: false, label: "Deactivate" },
];

const ActivateDeactivate = ({ value, title, statusChange }) => {
  const selectedOption =
    value && value.is_active
      ? options[0]
      : value && value.status
      ? options[0]
      : title
      ? title
      : options[1];

  const handleChange = (selectedOption) => {
    statusChange(value?.id, selectedOption.value);
  };

  return (
    <Select
      value={selectedOption}
      onChange={handleChange}
      options={options}
      isClearable={false}
      placeholder={title}
    />
  );
};

export default ActivateDeactivate;
