import { LOGIN, SET_AUTH_TOKEN, SAVE_EMAIL_PASSWORD, ADMIN_DETAILS} from "redux/actions/actionType";

const initialState = {
  token: null,
  email:"",
  password:"",
  adminDetails:{},
};
const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case LOGIN: {
      return {
        ...state,
        token: action.token,
      };
    }
    case SAVE_EMAIL_PASSWORD:{
      return {
        ...state,
        email:action.payload.email,
        password:action.payload.password,
      }
    }

    case ADMIN_DETAILS:{
      return {
        ...state,
        adminDetails:action.payload,
      }
    }
    case SET_AUTH_TOKEN:{
      return {
        ...state,
        token: action.payload,
      }
    }
    default: {
      return state;
    }
  }
};

export default authReducer;
