import React, { useCallback, useEffect, useState } from "react";
import { Images } from "shared/images";
import {
  BUTTON_TEXT,
  VERIFY_TOOLTIP,
  USERS_CONSTS,
  DOCUMENT_TYPE,
  SORT_KEY,
} from "shared/constants";
import ButtonStatus from "components/atom/button/ButtonStatus";
import { LOCUM_USER, PHARMACY_USER } from "shared/tableData";
import { Fade, Tooltip } from "@material-ui/core";
import DialogView from "components/atom/dialogview";
import {
  filterByDocumentType,
  getLocalizedDateString,
  splice,
  verifyStatusSwitch,
} from "components/utility";
import ActivateDeactivate from "components/molecules/activateDeactivate";

function UserTable({
  data,
  activeTab,
  userStatusChange,
  setViewDoc,
  setDocViewerParams,
  setmultiplePayload,
  multiplepayload,
  userView,
  sortKey,
  setSortKey,
}) {
  const [headerLabel, setHeaderLabel] = useState({});

  // Change heading title for table according to tabs
  const onTabChange = useCallback(() => {
    if (activeTab === USERS_CONSTS.PHARMACY_USERS.ID) {
      setHeaderLabel(PHARMACY_USER);
    } else {
      setHeaderLabel(LOCUM_USER);
    }
  }, [setHeaderLabel, activeTab]);

  useEffect(() => {
    onTabChange();
  }, [activeTab, onTabChange]);

  const sortSwitchKey = (name) => {
    switch (name) {
      case PHARMACY_USER.COMPANY_NAME:
        return setSortKey(
          sortKey === SORT_KEY.company_name
            ? SORT_KEY.default
            : SORT_KEY.company_name
        );
      case PHARMACY_USER.MANAGER_NAME || LOCUM_USER.NAME:
        return setSortKey(
          sortKey === SORT_KEY.first_name
            ? SORT_KEY.default
            : SORT_KEY.first_name
        );
      case PHARMACY_USER.CREATED_ON || LOCUM_USER.CREATED_ON:
        return setSortKey(
          sortKey === SORT_KEY.createdAt ? SORT_KEY.default : SORT_KEY.createdAt
        );
      case PHARMACY_USER.EMAIL || LOCUM_USER.EMAIL:
        return setSortKey(
          sortKey === SORT_KEY.email ? SORT_KEY.default : SORT_KEY.email
        );
      default:
        return setSortKey(SORT_KEY.default);
    }
  };

  return (
    <>
      <thead>
        <tr key={1000 + 1}>
          <th>
            <input
              type="checkbox"
              disabled={!multiplepayload.length}
              checked={multiplepayload.length}
              onChange={(e) => {
                if (multiplepayload.length) {
                  setmultiplePayload([]);
                }
              }}
            />
          </th>
          {Object.values(headerLabel).map((i, idx) => (
            <th scope="col" id={idx} onClick={() => sortSwitchKey(i)}>
              {i}
              {i === PHARMACY_USER.VERIFY_STATUS ? (
                <Tooltip
                  title={
                    <React.Fragment>
                      <DialogView>
                        {VERIFY_TOOLTIP.map((i, index) => {
                          return (
                            <div key={index} className="dropDown-list">
                              <img src={i.IMG} alt="" />
                              <span>{i.TITLE}</span>
                            </div>
                          );
                        })}
                      </DialogView>
                    </React.Fragment>
                  }
                  arrow={true}
                  placement={"bottom"}
                  enterDelay={200}
                  TransitionComponent={Fade}
                  TransitionProps={{ timeout: 300 }}
                >
                  <img src={Images.infoIcon} alt="info" />
                </Tooltip>
              ) : null}
            </th>
          ))}
        </tr>
      </thead>
      {data.length
        ? data.map((value, index) => (
            <tbody key={index}>
              <tr>
                <td>
                  <input
                    type="checkbox"
                    checked={multiplepayload.includes(value?.id)}
                    onChange={(e) => {
                      if (e.target.checked) {
                        setmultiplePayload((prev) => [...prev, value?.id]);
                      } else {
                        setmultiplePayload(splice(multiplepayload, value?.id));
                      }
                    }}
                  />
                </td>
                <td>{index + 1}</td>
                {activeTab === USERS_CONSTS.PHARMACY_USERS.ID ? (
                  <>
                    <td>{value?.first_name + " " + value?.last_name}</td>
                    <td>
                      {value?.profile_data?.company_name}
                      <img alt="" src={Images.verifyLogo} />
                    </td>
                    <td>{value?.email}</td>
                  </>
                ) : (
                  <>
                    <td>
                      {value?.first_name + " " + value?.last_name}
                      <img alt="" src={Images.verifyLogo} />
                    </td>
                    <td>{value?.email}</td>
                    <td>{value?.phone ? value?.phone : ""}</td>
                  </>
                )}

                <td>
                  {getLocalizedDateString(value?.profile_data?.created_at)}
                </td>

                <td>
                  <ActivateDeactivate
                    statusChange={userStatusChange}
                    value={value}
                  />
                </td>

                <td>
                  <div className="d-flex">
                    <ButtonStatus
                      image={Images.education}
                      status={verifyStatusSwitch(
                        filterByDocumentType(
                          value.document_data,
                          DOCUMENT_TYPE.EDUCATION
                        )
                      )}
                      title={BUTTON_TEXT.EDUCATION}
                      onClick={() => {
                        setViewDoc(true);
                        setDocViewerParams([
                          value?.id,
                          DOCUMENT_TYPE.EDUCATION,
                          filterByDocumentType(
                            value.document_data,
                            DOCUMENT_TYPE.EDUCATION
                          ),
                        ]);
                      }}
                    />
                    <ButtonStatus
                      image={Images.license}
                      status={verifyStatusSwitch(
                        filterByDocumentType(
                          value.document_data,
                          DOCUMENT_TYPE.LICENSE
                        )
                      )}
                      title={BUTTON_TEXT.LICENSE}
                      onClick={() => {
                        setViewDoc(true);
                        setDocViewerParams([
                          value?.id,
                          DOCUMENT_TYPE.LICENSE,
                          filterByDocumentType(
                            value.document_data,
                            DOCUMENT_TYPE.LICENSE
                          ),
                        ]);
                      }}
                    />
                    <ButtonStatus
                      image={Images.insurance}
                      status={verifyStatusSwitch(
                        filterByDocumentType(
                          value.document_data,
                          DOCUMENT_TYPE.INSURANCE
                        )
                      )}
                      title={BUTTON_TEXT.INSURANCE}
                      onClick={() => {
                        setViewDoc(true);
                        setDocViewerParams([
                          value?.id,
                          DOCUMENT_TYPE.INSURANCE,
                          filterByDocumentType(
                            value.document_data,
                            DOCUMENT_TYPE.INSURANCE
                          ),
                        ]);
                      }}
                    />
                    <ButtonStatus
                      image={Images.identity}
                      status={verifyStatusSwitch(
                        filterByDocumentType(
                          value.document_data,
                          DOCUMENT_TYPE.IDENTITY
                        )
                      )}
                      title={BUTTON_TEXT.IDENTITY}
                      onClick={() => {
                        setViewDoc(true);
                        setDocViewerParams([
                          value?.id,
                          DOCUMENT_TYPE.IDENTITY,
                          filterByDocumentType(
                            value.document_data,
                            DOCUMENT_TYPE.IDENTITY
                          ),
                        ]);
                      }}
                    />
                  </div>
                </td>

                <td>
                  <div>
                    <ButtonStatus
                      image={Images.viewIcon}
                      status={"default"}
                      title={BUTTON_TEXT.VIEW}
                      onClick={() => userView(value.id)}
                    />
                  </div>
                </td>
              </tr>
            </tbody>
          ))
        : "No Data Availiable"}
    </>
  );
}

export default UserTable;
